#loadingPage {
  color: #1898d9;
}
#loadingPage .loading {
    margin: 60% 40% 5px;
}

#loading .loader {
  position: relative;
  width: 5rem;
  height: 5rem;
}
#loading .loader.circle-line span {
    position: absolute;
    display: inline-block;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    background: #1898d9;
    -webkit-animation: circle-line 1s ease infinite;
    animation: circle-line 1s ease infinite;
}
#loading .loader.circle-line span:nth-child(1) {
      top: 50%;
      left: 0;
      margin-top: -0.25rem;
      -webkit-animation-delay: 0.125s;
              animation-delay: 0.125s;
}
#loading .loader.circle-line span:nth-child(2) {
      top: 1rem;
      left: 0.5rem;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
      -webkit-animation-delay: 0.25s;
              animation-delay: 0.25s;
}
#loading .loader.circle-line span:nth-child(3) {
      top: 0.5rem;
      left: 50%;
      margin-left: -0.75rem;
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
      -webkit-animation-delay: 0.375s;
              animation-delay: 0.375s;
}
#loading .loader.circle-line span:nth-child(4) {
      top: 1rem;
      right: 1.5rem;
      -webkit-transform: rotate(145deg);
              transform: rotate(145deg);
      -webkit-animation-delay: 0.5s;
              animation-delay: 0.5s;
}
#loading .loader.circle-line span:nth-child(5) {
      top: 50%;
      left: 3.5rem;
      margin-top: -0.25rem;
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
      -webkit-animation-delay: 0.625s;
              animation-delay: 0.625s;
}
#loading .loader.circle-line span:nth-child(6) {
      bottom: 1rem;
      right: 1.5rem;
      -webkit-transform: rotate(-145deg);
              transform: rotate(-145deg);
      -webkit-animation-delay: 0.75s;
              animation-delay: 0.75s;
}
#loading .loader.circle-line span:nth-child(7) {
      bottom: 0.5rem;
      left: 50%;
      margin-left: -15px;
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      -webkit-animation-delay: 0.875s;
              animation-delay: 0.875s;
}
#loading .loader.circle-line span:nth-child(8) {
      bottom: 1rem;
      left: 0.5rem;
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
      -webkit-animation-delay: 1s;
              animation-delay: 1s;
}
@-webkit-keyframes circle-line {
0% {
    opacity: 0.8;
}
100% {
    width: 0.3rem;
    height: 0.3rem;
    opacity: 0.3;
}
}
@keyframes circle-line {
0% {
    opacity: 0.8;
}
100% {
    width: 0.3rem;
    height: 0.3rem;
    opacity: 0.3;
}
}

